// 
export const addFavoriteTrail = async (trailId) => {
  try {
    const { data } = await axiosAuthInstance.post(`/trails/favorites`, {
      trailId
    })
    if (data) {
      return { success: true }
    }
  } catch (error) {
    console.error('[Add Favorite Trail Failed]:', error)
    return { success: false }
  }
}

